import React from "react";
import loadImage from "blueimp-load-image";
import Plot from "react-plotly.js";
import { FileBlobOrURL } from "../Types";
import { conditionalExpression } from "@babel/types";
import generate from "@babel/generator";

interface Props {
  imageFile: FileBlobOrURL;
  onError: (errorMessage: string) => void;
}

interface State {
  x: number[];
  y: number[];
  z: number[];
  colors: string[];
}
const rgbToHSL = (r: number, g: number, b: number) => {
  // https://css-tricks.com/converting-color-spaces-in-javascript/
  // Make r, g, and b fractions of 1
  r /= 255;
  g /= 255;
  b /= 255;

  // Find greatest and smallest channel values
  const cmin = Math.min(r, g, b);
  const cmax = Math.max(r, g, b);
  const delta = cmax - cmin;
  let h = 0;
  let s = 0;
  let l = 0;

  // Calculate hue
  // No difference
  if (delta === 0) h = 0;
  // Red is max
  else if (cmax === r) h = ((g - b) / delta) % 6;
  // Green is max
  else if (cmax === g) h = (b - r) / delta + 2;
  // Blue is max
  else h = (r - g) / delta + 4;

  h = h * 60;

  // Make negative hues positive behind 360°
  if (h < 0) h += 360;

  // Calculate lightness
  l = (cmax + cmin) / 2;

  // Calculate saturation
  s = delta === 0 ? 0 : delta / (1 - Math.abs(2 * l - 1));

  return [h, s, l];
};

const hslToRGB = (h: number, s: number, l: number) => {
  const c = (1 - Math.abs(2 * l - 1)) * s;
  const x = c * (1 - Math.abs(((h / 60) % 2) - 1));
  const m = l - c / 2;
  let r = 0;
  let g = 0;
  let b = 0;

  h %= 360;
  if (0 <= h && h < 60) {
    r = c;
    g = x;
    b = 0;
  } else if (60 <= h && h < 120) {
    r = x;
    g = c;
    b = 0;
  } else if (120 <= h && h < 180) {
    r = 0;
    g = c;
    b = x;
  } else if (180 <= h && h < 240) {
    r = 0;
    g = x;
    b = c;
  } else if (240 <= h && h < 300) {
    r = x;
    g = 0;
    b = c;
  } else if (300 <= h && h < 360) {
    r = c;
    g = 0;
    b = x;
  }
  r = Math.round((r + m) * 255);
  g = Math.round((g + m) * 255);
  b = Math.round((b + m) * 255);

  return [r, g, b];
};

const hslToXYZ = (h: number, s: number, l: number) => {
  const z = l - 0.5;
  const angle = (h / 180) * Math.PI;
  const radius = Math.sqrt(0.5 ** 2 - z ** 2) * s;
  const x = Math.sin(angle) * radius;
  const y = Math.cos(angle) * radius;
  return [x, y, z];
};

const generateHueGuideLine = () => {
  const N_POINTS = 256;

  const xs = [];
  const ys = [];
  const zs = [];
  const cs = [];
  for (let i = 0; i < N_POINTS; ++i) {
    const h = (i / (N_POINTS - 1)) * 360;
    const s = 1.0;
    const l = 0.5;
    const [r, g, b] = hslToRGB(h, s, l);
    const [x, y, z] = hslToXYZ(h, s, l);
    xs.push(x);
    ys.push(y);
    zs.push(z);
    cs.push(`rgb(${r}, ${g}, ${b})`);
  }
  return [xs, ys, zs, cs];
};

class HSLPlot extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      x: [],
      y: [],
      z: [],
      colors: []
    };

    loadImage(
      props.imageFile,
      data => {
        if (data instanceof Event) {
          this.props.onError("Loading image file failed");
        } else if (data instanceof HTMLImageElement) {
          alert("image!");
        } else {
          this.analyzeImage(data);
        }
      },
      {
        maxWidth: 100,
        maxHeight: 100,
        canvas: true
      }
    );
  }

  analyzeImage(imageCanvas: HTMLCanvasElement) {
    const ctx = imageCanvas.getContext("2d");
    if (ctx === null) {
      alert("error");
      return;
    }
    const pixels = ctx.getImageData(0, 0, imageCanvas.width, imageCanvas.height)
      .data;
    const xs = [];
    const ys = [];
    const zs = [];
    const colors = [];
    for (let i = 0; i < pixels.length; i += 4) {
      const [r, g, b] = [pixels[i], pixels[i + 1], pixels[i + 2]];
      const [h, s, l] = rgbToHSL(r, g, b);
      const [x, y, z] = hslToXYZ(h, s, l);
      xs.push(x);
      ys.push(y);
      zs.push(z);

      /*
        xs.push(r);
        ys.push(g);
        zs.push(b);
        */
      colors.push(`rgb(${r}, ${g}, ${b})`);
    }
    this.setState({
      x: xs,
      y: ys,
      z: zs,
      colors
    });
  }

  // https://codepen.io/nicolaskruchten/pen/ERgBZX
  render() {
    // TODO: bettern ame
    const [xs, ys, zs, cs] = generateHueGuideLine();

    if (this.state.x.length === 0) {
      return (
        <div className="d-flex justify-content-center">
          <div className="spinner-border" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <Plot
            data={[
              {
                x: this.state.x,
                y: this.state.y,
                z: this.state.z,
                type: "scatter3d",
                mode: "markers",
                marker: { color: this.state.colors, size: 3 }
              },
              {
                x: [0, 0],
                y: [0, 0],
                z: [-0.5, +0.5],
                type: "scatter3d",
                mode: "lines",
                line: {
                  color: "rgb(200, 200, 200)"
                }
              },
              {
                x: xs,
                y: ys,
                z: zs,
                type: "scatter3d",
                mode: "lines",
                line: {
                  color: cs
                }
              }
            ]}
            layout={{
              showlegend: false,
              scene: {
                xaxis: { range: [-0.5, 0.5] },
                yaxis: { range: [-0.5, 0.5] },
                zaxis: { range: [-0.5, 0.5] },
                aspectmode: "cube"
              },
              autosize: true,
              margin: {
                l: 0,
                r: 0,
                b: 0,
                t: 0
              }
            }}
            useResizeHandler={true}
            style={{
              width: "100%"
            }}
          />
          c.f.{" "}
          <a href="https://en.wikipedia.org/wiki/Color_solid">color solid</a>
        </div>
      );
    }
  }
}

export default HSLPlot;
